import React from "react";
import { render } from "react-dom";
import ReactDOM from 'react-dom';
import './index.css'; // Tell webpack that Button.js uses these styles

const node = document.getElementById("root");



class Square extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 0,
    };
  }
  render() {
    return (
      // Buttons have built in event listeners - so we have that going
      // Simply give the button a user defined function to run when a click 
      // is detected 
      <button className="square" onClick={() => this.props.onClick()  } >
        {this.props.value}
      </button>
    );
  }
}

class Board extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      squares: Array(9).fill(null),
      player1Turn: true
    };
  }
  
  // traverse the board to check for wins!
  checkWin() {
	
  }


  handleClick(i) {
    
    // Copy the data structure with .slice 
    const squares = this.state.squares.slice();
    
    if ( (this.state.player1Turn == true) && (squares[i] == null) ) {
      squares[i] = 'X';
      this.state.player1Turn = !this.state.player1Turn;
    }
    else if ( (squares[i] == null) ){
      squares[i] = 'O';
      this.state.player1Turn = !this.state.player1Turn;
    }
    else {
	// The user is trying to play where a piece already is
	alert("Illegal Move ya cotton headed ninny muggin!");
    }
	
    // Update the state of the component
    this.setState({squares: squares});
  	
    this.checkWin();
  }
  
  renderSquare(i) {
    return <Square value={this.state.squares[i] } onClick={() => this.handleClick(i)} />;
  }
  

  render() {
    const status = 'Welcome to Tic Tac Toe';

    return (
      <div>
        <div className="status">{status}</div>
        <div className="board-row">
          {this.renderSquare(0)}
          {this.renderSquare(1)}
          {this.renderSquare(2)}
        </div>
        <div className="board-row">
          {this.renderSquare(3)}
          {this.renderSquare(4)}
          {this.renderSquare(5)}
        </div>
        <div className="board-row">
          {this.renderSquare(6)}
          {this.renderSquare(7)}
          {this.renderSquare(8)}
        </div>
      </div>
    );
  }
}

